<template>
  <div id="Confirm" class="layout">
    <p class="tit">Confirm 确认提示框（方法 h_confirm）</p>
    <div class="base_box">
      <h-button :butContent="'确认提示框'" @click="ConfirmFn()" />
    </div>
  </div>
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const ConfirmFn = function () {
      proxy
        .h_confirm({
          title: "提示标题",
          content:
            "提示内容自定义，全局使用,提示内容自定义，全局使用提示内容自定义，全局使用提示内容自定义，全局使用",
          style: {
            box: {
              top: 30,
              bgColor: "#fff",
              width: 400,
              padding: 10,
              borderRadius: 0,
            },
            title: {
              // tittextAlign:'left',
              titColor: "red",
              // titlineHeight:30,
              titfontSize: 22,
              titfontWeight: 100,
            },
            content: {
              // contextAlign:'left',
              // conColor:'blue',
              // conlineHeight:22,
              // confontSize:14,
              // confontWeight:100
            },
          },
          buttonConfigs: {
            yes: "确定",
            no: "取消",
          },
        })
        .then(() => {
          // 自定义弹框
          proxy.h_tips({
            type: "success",
            msg: "确认",
            top: 30,
            time: 1000,
          });
        })
        .catch(() => {
          // 自定义弹框
          proxy.h_tips({
            type: "info",
            msg: " 取消",
            top: 30,
            time: 1000,
          });
        });
    };
    return {
      ConfirmFn,
    };
  },
};
</script>

<style lang="less"></style>
